"use client";

import { usePathname, useRouter, useSearchParams } from "next/navigation";
import { MagnifyingGlassIcon, CalendarIcon, CurrencyDollarIcon } from "@heroicons/react/24/outline";
import { useDebouncedCallback } from "use-debounce";
import { useState } from "react";

interface SearchProps {
  placeholder: string;
  showDateFilter?: boolean;
  showPriceFilter?: boolean;
}

export default function Search({ 
  placeholder, 
  showDateFilter = true, 
  showPriceFilter = true 
}: SearchProps) {
  const searchParams = useSearchParams();
  const router = useRouter();
  const pathname = usePathname();

  // Get current values from URL
  const defaultQuery = searchParams.get("query") || "";
  const defaultDateFrom = searchParams.get("dateFrom") || "";
  const defaultDateTo = searchParams.get("dateTo") || "";
  const defaultPriceMin = searchParams.get("priceMin") || "";
  const defaultPriceMax = searchParams.get("priceMax") || "";

  // Local state for filters
  const [showFilters, setShowFilters] = useState(false);

  // Generic function to update URL parameters
  const updateSearchParams = useDebouncedCallback((updates: Record<string, string>) => {
    const params = new URLSearchParams(searchParams);
    
    // Reset to page 1 when search changes
    params.set("page", "1");
    
    // Apply all updates
    Object.entries(updates).forEach(([key, value]) => {
      if (value && value.trim()) {
        params.set(key, value);
      } else {
        params.delete(key);
      }
    });

    router.replace(`${pathname}?${params.toString()}`);
  }, 300);

  const handleSearch = (term: string) => {
    updateSearchParams({ query: term });
  };

  const handleDateChange = (field: 'dateFrom' | 'dateTo', value: string) => {
    updateSearchParams({ [field]: value });
  };

  const handlePriceChange = (field: 'priceMin' | 'priceMax', value: string) => {
    updateSearchParams({ [field]: value });
  };

  const clearFilters = () => {
    updateSearchParams({
      query: "",
      dateFrom: "",
      dateTo: "",
      priceMin: "",
      priceMax: ""
    });
  };

  const hasActiveFilters = defaultQuery || defaultDateFrom || defaultDateTo || defaultPriceMin || defaultPriceMax;

  return (
    <div className="space-y-4">
      {/* Main Search Bar */}
      <div className="relative flex flex-1 flex-shrink-0">
        <label htmlFor="search" className="sr-only">
          Search
        </label>
        <input
          id="search"
          defaultValue={defaultQuery}
          className="peer block w-full rounded-md border border-gray-200 py-[9px] pl-10 pr-20 text-sm outline-2 placeholder:text-gray-500 focus:border-blue-500 focus:outline-none focus:ring-1 focus:ring-blue-500"
          placeholder={placeholder}
          onChange={(e) => handleSearch(e.target.value)}
        />
        <MagnifyingGlassIcon className="absolute left-3 top-1/2 h-[18px] w-[18px] -translate-y-1/2 text-gray-500 peer-focus:text-gray-900" />
        
        {/* Filter Toggle Button */}
        <button
          onClick={() => setShowFilters(!showFilters)}
          className="absolute right-2 top-1/2 -translate-y-1/2 rounded-md px-2 py-1 text-xs text-gray-500 hover:bg-gray-100 hover:text-gray-700"
        >
          Filters {hasActiveFilters && <span className="ml-1 h-2 w-2 rounded-full bg-blue-500 inline-block"></span>}
        </button>
      </div>

      {/* Advanced Filters */}
      {showFilters && (
        <div className="rounded-lg border border-gray-200 bg-gray-50 p-4">
          <div className="grid grid-cols-1 gap-4 md:grid-cols-2 lg:grid-cols-4">
            
            {/* Date Range Filter */}
            {showDateFilter && (
              <>
                <div>
                  <label htmlFor="dateFrom" className="block text-sm font-medium text-gray-700 mb-1">
                    Date From
                  </label>
                  <div className="relative">
                    <input
                      id="dateFrom"
                      type="date"
                      defaultValue={defaultDateFrom}
                      className="block w-full rounded-md border border-gray-200 py-2 pl-10 pr-3 text-sm focus:border-blue-500 focus:outline-none focus:ring-1 focus:ring-blue-500"
                      onChange={(e) => handleDateChange('dateFrom', e.target.value)}
                    />
                    <CalendarIcon className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
                  </div>
                </div>

                <div>
                  <label htmlFor="dateTo" className="block text-sm font-medium text-gray-700 mb-1">
                    Date To
                  </label>
                  <div className="relative">
                    <input
                      id="dateTo"
                      type="date"
                      defaultValue={defaultDateTo}
                      className="block w-full rounded-md border border-gray-200 py-2 pl-10 pr-3 text-sm focus:border-blue-500 focus:outline-none focus:ring-1 focus:ring-blue-500"
                      onChange={(e) => handleDateChange('dateTo', e.target.value)}
                    />
                    <CalendarIcon className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
                  </div>
                </div>
              </>
            )}

            {/* Price Range Filter */}
            {showPriceFilter && (
              <>
                <div>
                  <label htmlFor="priceMin" className="block text-sm font-medium text-gray-700 mb-1">
                    Min Price
                  </label>
                  <div className="relative">
                    <input
                      id="priceMin"
                      type="number"
                      min="0"
                      step="0.01"
                      defaultValue={defaultPriceMin}
                      placeholder="0"
                      className="block w-full rounded-md border border-gray-200 py-2 pl-10 pr-3 text-sm focus:border-blue-500 focus:outline-none focus:ring-1 focus:ring-blue-500"
                      onChange={(e) => handlePriceChange('priceMin', e.target.value)}
                    />
                    <CurrencyDollarIcon className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
                  </div>
                </div>

                <div>
                  <label htmlFor="priceMax" className="block text-sm font-medium text-gray-700 mb-1">
                    Max Price
                  </label>
                  <div className="relative">
                    <input
                      id="priceMax"
                      type="number"
                      min="0"
                      step="0.01"
                      defaultValue={defaultPriceMax}
                      placeholder="No limit"
                      className="block w-full rounded-md border border-gray-200 py-2 pl-10 pr-3 text-sm focus:border-blue-500 focus:outline-none focus:ring-1 focus:ring-blue-500"
                      onChange={(e) => handlePriceChange('priceMax', e.target.value)}
                    />
                    <CurrencyDollarIcon className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-gray-400" />
                  </div>
                </div>
              </>
            )}
          </div>

          {/* Clear Filters Button */}
          {hasActiveFilters && (
            <div className="mt-4 flex justify-end">
              <button
                onClick={clearFilters}
                className="rounded-md bg-gray-200 px-3 py-1 text-sm text-gray-700 hover:bg-gray-300"
              >
                Clear All Filters
              </button>
            </div>
          )}
        </div>
      )}
    </div>
  );
}